import React from 'react';
import Card from './components/Card';
import Cart from './components/Cart';
import Navbar from './components/Navbar';
import { connect } from 'react-redux';
import { getItems,  itemDetails} from './redux/action';
import header from './images/1.png';
import Footer from './components/Footer';
import Modal from './components/Modal';

class Home extends React.Component{
    componentDidMount(){
        this.props.getItems();
    }
    setModal(item){
        this.props.itemDetails(item);
    }
    render(){
        return(
            <>
            <Navbar />
            <div className="container-fluid bg-dark" style={{background: "url(./img/bg.png)", textAlign:"center"}}>
                <img src={header} alt="header" style={{filter:"invert(90)",width:"80vw",maxWidth:"500px"}}/>
            </div>
            <Cart />
            <div className="container-fluid">
                <div className="row justify-content-around">
                    {this.props.items.map(item=>{
                        return <Card item={item} click={()=>this.setModal(item)} key={item.title} />
                    })}
                </div>
            </div>
            <Footer />
            <Modal />
            </>
        )
    }
}

const setProps=(state)=>{
    return{
        items: state.items,
        cart: state.cart
    }
}

export default connect(setProps, {getItems, itemDetails})(Home);